#root{
    position: relative;
    z-index: -1;
}
body{
    background-color: rgb(41, 41, 41);
    position: relative;
    z-index: -1;
    margin: 0;
    font-family:monospace;
    color: white;
}
.nav{
    border-bottom:  rgb(93, 93, 156) solid 2px;
    background-color: rgb(41, 41, 41);
    /*width: var(--barwidth);
    position: fixed;
    height: 100%;*/
    & a{
        font-size: 20px;
        padding: 10px;
        display: inline-block;
        color: white;
        cursor: pointer;
        text-decoration: none;
    }
    & a:hover{
        background-color: rgb(93, 93, 156);
    }
}
.content{
    box-sizing: border-box;
    background-color: rgb(41, 41, 41);
    z-index: -1;
    position: relative;
}
.line{
    width: 95%;
    margin: 0 auto;
    border-top:  rgb(93, 93, 156) solid 2px;
}
.line2{
    width: 100%;
    margin: 0 auto;
    border-top:  rgb(93, 93, 156) solid 2px;
}
h1{
    font-size: 60px;
    margin-left: 5%;
    padding-top: 40px;
    margin-top: 0;
}
@media screen and (min-width: 862px){
    /*PC*/
    .box1{
        width: 50%;
    }
    .box2{
        width: 50%;
        background-position: right;
    }
    .homeFooter2{
        display: none;
    }
    .homeLine{
        display: none;
    }
    .caroussel{
        width: 600px;
        height: 640px;
        margin: 55px auto;
        & .image{        
            height: 500px;
        }
        & .desc{
            width: 380px;
            border-right: 4px solid rgb(41, 41, 41);
        }
        & .gitbtn{
            margin: 14px 16px 0px 0px;
        }
    }
    .back{
        width: 680px;
        height: 600px;
        top: 225px;
        & button{
            font-size: 120px;
            top: 220px;
        }
        & .rightbtn{
            left: 630px;
        }
        & .leftbtn{
            left: -80px;
        }
    }
    .proj1{
        & img{
            height: 700px;
            right: 90px;
        }
    }
    .proj2{
        & img{
            max-height: 627px;
        }
    }
    .proj3{
        & img{
            right: 132px;
            max-height: 500px;
        }
    }
}
@media screen and (max-width: 862px){
    /*MOBILE*/
    .box1{
        min-width: 373px;
    }
    .box2{
        width: 100%;
        background-position: center;
        height: 300px;
    }
    .homeFooter{
        display: none;
    }
    .caroussel{
        width: 320px;
        height: 540px;
        margin: 76px auto 86px auto;
        & .image{        
            height: 380px;
        }
        & .desc{
            width: 330px;
            border-right: none;
        }
        & .gitbtn{
            margin: 22px 16px 0px 0px;
        }
    }
    .back{
        width: 375px;
        height: 500px;
        top: 248px;
        & button{
            font-size: 50px;
            top: -74px;
        }
        & .rightbtn{
            left: 310px;
        }
        & .leftbtn{
            left: 6px;
        }
        /**/
    }
    .proj1{
        & img{
            height: 590px;
            right: 160px;
        }
    }
    .proj2{
        & img{
            max-height: 462px;
        }
    }
    .proj3{
        & img{
            right: 185px;
            max-height: 400px;
        }
    }
}
