.flex{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 auto;
    min-height: 100vh;
    & .box1{
        & h1{
            margin: 0 10%;
            padding-top: 50px;
            padding-bottom: 20px;
            font-size: 60px;
        }
        & .summary{
            & h2{
                margin: 20px 5%;
                font-size: 40px;
                /*color: rgb(226, 185, 71);*/
                color:white;
                display: inline-block;
            }
            & button{
                border: none;
                font-size: 30px;
                background-color: rgb(41, 41, 41);
                color: white;
                cursor: pointer;
                position: relative;
                float: right;
                top: 25px;
                right: 15px;
            }
            & p{
                display: none;
                padding: 0 5%;
                margin-top: 0;
                font-size: 25px;
            }
        }
    }
    & .box2{
        overflow: hidden;
        background-image: url("../images/selfie.jpg");
        background-attachment: fixed;
        background-repeat: no-repeat;
        background-size: 100vh;
        box-sizing: border-box;
        & h1{
            position: absolute;
            z-index: 1;
            color: rgb(41, 41, 41);
            text-decoration: underline;
            text-decoration-color: rgb(93, 93, 156);
        }
    }
}
.back{
    background-color: rgb(51, 51, 85);
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 20px;
    z-index: -1;
    
    & button{
        border: none;
        color: white;
        background-color: rgba(0,0,0,0);
        cursor: pointer;
        position: absolute;
    }
    
}
.proj1{
    display: block;
    & img{
        position: relative;
        bottom: 150px;
        display: block;
        margin: 0 auto;
    }
}
.proj2{
    display: none;
    & img{
        position: relative;
        display: block;
        margin: 0 auto;
    }
}
.proj3{
    display: none;
    & img{
        position: relative;
        bottom: 0px;
        display: block;
        margin: 0 auto;
    }
}
.caroussel{
    position: relative;
    border-radius: 20px;
    background-color: rgb(93, 93, 156);
    & h2{
        padding-top: 10px;
        margin-bottom: 10px;
        margin-left: 20px;
    }
    & .image{
        position: relative;
        z-index: 1;
        /*background-image: 
        linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.5));*/
        overflow: hidden;
        margin: 0;
        border-top: 4px solid rgb(41, 41, 41);
        border-bottom: 4px solid #292929;
    }
    & .desc{
        float: left;
        height: 89px;
        padding: 10px 10px 0px 10px;
        font-size: 18px;
        box-sizing: border-box;
    }
    & .gitbtn{
        width: 190px;
        height: 60px;
        background-color: rgb(43, 43, 105);
        color: white;
        border-radius: 20px;
        font-size: 18px;
        cursor: pointer;
        float: right;
        text-align: center;
        text-decoration: none;
        line-height: 60px;
    }
    & .gitbtn:hover{
        background-color: rgb(21, 21, 48);
        color: yellow;
    }
}
.tabout{
    margin: 15px 10%;
    font-size: 20px;
    width: 80%;
    & td{
        height: 35px;
    }
}
.education{
    font-size: 25px;
    width: 80%;
    margin: 20px auto;
}
.img1{
    background-image: 
        linear-gradient(to right, rgba(93, 93, 156, .5), rgba(107, 94, 122, 0.5)),
        url("../images/piramide.jpg");
    
    filter: blur(2px);
    -webkit-filter: blur(2px);
    min-height: 300px;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.img2{
    background-image: 
        linear-gradient(to right, rgba(93, 93, 156, .5), rgba(107, 94, 122, 0.5)),
        url("../images/techo.jpg");
    filter: blur(2px);
    -webkit-filter: blur(2px);
    min-height: 300px;
    background-attachment: fixed;
    background-position: 50% 15%;
    background-repeat: no-repeat;
    background-size: cover;
}
.img3{
    background-image: 
        linear-gradient(to right, rgba(93, 93, 156, .5), rgba(107, 94, 122, 0.5)),
        url("../images/selfie.jpg");
    filter: blur(2px);
    -webkit-filter: blur(2px);
    min-height: 300px;
    background-attachment: fixed;
    background-position: 50% 75%;
    background-repeat: no-repeat;
    background-size: cover;
}
.sectionTitle{
    position: relative;
    & .title{
        position: absolute;
        font-size: 60px;
        font-weight: bold;
        bottom: 60px;
        margin-left: 60px;
    }
}
footer{
    color: white;
    position: relative;
    bottom: 0;
    & h2{
        margin: 20px 5%;
        font-size: 40px;
        display: inline-block;
    }
    & button{
        border: none;
        font-size: 30px;
        background-color: rgb(41, 41, 41);
        color: white;
        cursor: pointer;
        position: relative;
        float: right;
        top: 25px;
        right: 15px;
    }
    & .links{
        margin-left: 12%;
        padding-bottom: 10px;
        display: none;
        & td{
            font-size: 20px;
        }
        & a{
            margin-right: 5px;
            color: white;
        }
        & a:hover{
            color: yellow;
        }

    }
    & .linksA{
        margin-left: 8%;
        padding-bottom: 10px;
        & td{
            font-size: 20px;
        }
        & a{
            margin-right: 5px;
            color: white;
            text-decoration: none;
        }
        & a:hover{
            color: yellow;
        }

    }
    .tooltip {
        position: relative;
        display: inline-block;
      }
      
      .tooltip .tooltiptext {
        font-size: 15px;
        visibility: hidden;
        width: 140px;
        background-color: #555;
        color: #fff;
        text-align: center;
        border-radius: 6px;
        padding: 5px;
        position: absolute;
        z-index: 1;
        bottom: 150%;
        left: 50%;
        margin-left: -75px;
        opacity: 0;
        transition: opacity 0.3s;
      }
      .tooltip:hover .tooltiptext {
        visibility: visible;
        opacity: 1;
      }
}